body {
  margin: 0;
  background-color: #f2f6ff;
  font-family: "Raleway", sans-serif;
}

/* style for header */

nav {
  background-color: rgb(255, 255, 255);
  padding: 16px 78px;
  display: flex;
  border-bottom: 1px solid #ccc;
}

#logo {
  margin: 0;
  font-size: 32px;
  color: #0084ff;
  letter-spacing: -1px;
  font-weight: 900;
}

.nav-cat {
  margin-left: 74px;
  color: #000;
  font-weight: 500;
}

.drop-icon {
  margin-left: 8px;
}

.logo-story {
  margin: 0;
  font-style: italic;
  line-height: 9px;
  font-weight: 500;
}

/* style for category */

#section-wrapper {
  padding: 30px 230px;
}

#cat-wrapper {
  padding-top: 20px;
  font-size: 12px;
  margin-bottom: 20px;
}

.cat-heading {
  margin: 0;
  color: #000;
  font-weight: 600;
  font-size: 24px;
}

.cat-filter {
  font-size: 14px;
  font-weight: 600;
}

.cat-item-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.cat-item {
  padding: 6px 14px;
  color: #0084ff;
  border: 1px solid #0084ff;
  border-radius: 15px;
  margin: 5px;
  cursor: pointer;
}

.active-item {
  color: #fff;
  background-color: #0084ff;
}

.filter-icon {
  margin-right: 8px;
}

/* style for cards with image */

.card-wrapper {
  width: 280px;
  background-color: white;
  border: 1px solid rgba(0, 168, 255, 0.3);
  border-radius: 5px;
  box-shadow: 0px 0px 5px #ccc;
  display: inline-block;
  margin: 8px;
}

.img-section {
  width: 100%;
  height: 176px;
}

.card-image {
  width: 100%;
  height: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.content-wrapper {
  padding: 10px 24px;
}

.heading-text {
  font-size: 18px;
  margin: 0;
  text-align: left;
  font-weight: 600;
}

.content-para {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.43;
  color: rgba(0, 0, 0, 0.5);
}

.card-author-date-wrapper {
  margin-top: 10px;
}

.card-author {
  font-size: 14px;
  font-weight: 500;
  font-style: italic;
  color: #0096ff;
}

.card-date {
  font-size: 14px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.95);
  margin-left: 3px;
}

/* footer styling */

#footer {
  background-color: #173146;
  padding: 80px 16%;
}

.padding {
  padding: 30px 230px;
}



